import React from 'react'

class Home extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      tableValue: [],
      priorityQueueOpen: [{index: 300, f: 0}],
      priorityQueueClose: []
    }
  }

  row = (index) => {
    let row = [];
    for (let i = 0; i < 20; i++) {
      row.push(<div 
        onClick={e => {
          this.setState((prev) => {
          prev.tableValue = []
          prev.tableValue[index + i] = -1
          return { tableValue: prev.tableValue }
        })
      }} 
        key={index + i} 
        style={{
          width: "30px", 
          height: "30px", 
          border: "1px solid #000000", 
          display: "inline-block", 
          marginLeft:"3px", 
          marginBottom:"3px",
          textAlign: "center",
          lineHeight: "30px",
          color:"white",
          fontWeight: "bolder",
          backgroundColor: (i + index) == 300 ? "red" : (this.state.tableValue[index + i] == -1 ? "green" : "white"),
          verticalAlign: "top"}}>
          {this.state.tableValue[index + i]}
          </div>)
    }
    return row
  }
  
  table = () => {
    let table = [];
    for (let i = 0; i < 20; i++) {
      table.push(<div style={{marginLeft: "30px"}} key={"row" + i}> { this.row( 20*i ) } </div>)
    }
    return table
  }

  pathfinding = () => {
    while (this.state.priorityQueueOpen.length !== 0) {
      let q = this.state.priorityQueueOpen.pop()
      let x = q.index % 20;
      let y = (q - q.index % 20)/20;
      console.log(x, y)
    }
  }

  render() {
    return (
      <div>
        {
          this.table()
        }
      </div>
    )
  }
}

export default Home